import React, { Fragment, useState} from "react";
import PropTypes from "prop-types";
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import { withStyles } from "@material-ui/core";
import ReactMapGL, { Marker,Popup } from "react-map-gl";
import FormControl from '@material-ui/core/FormControl';
import MenuItem from '@material-ui/core/MenuItem';
import Select from '@material-ui/core/Select';
import InputLabel from '@material-ui/core/InputLabel';
import { Room} from "@material-ui/icons";

const styles = (theme) => ({
  mapboxglpopup: {
    maxWidth: 400,
    fontWeight: 12,
  },
  bubble: {
      flexDirection: 'column',
      alignSelf: 'flex-start',
      backgroundColor: '#fff',
      borderRadius: 6,
      borderColor: '#ccc',
      borderWidth: 0.5,
      padding: 15,
      width: 150,
    },
    formControl: {
      margin: 30,
      minWidth: 200,
    },
});


const AddPostOptions = ({ 
        handleTitleChange,
        handleDescriptionChange,
        handleCityChange,
        handlePinCodeChange,
        handleStateChange,
        handleAmountChange,
        handleLandMarkChange,  
        handleDestinationChange,
        classes
      }) => {

        const loc = {
          latitude: 31.5892,
          longitude: 76.9182,
        }
        const [viewport, setViewport] = useState({
          latitude: 31.5892,
          longitude: 76.9182,
          zoom: 6,
        });

        const [place, setPlace] = useState(null)
        const [showPopup1, setShowPopup1] = useState(false);
        const [showPopup2, setShowPopup2] = useState(false);
        const handleAddClick = (e) => {
          const [longitude, latitude] = e.lngLat;
          setPlace({
            coordinate: {
              latitude: latitude,
              longitude: longitude,
            }
          });
          handleDestinationChange(e);
          console.log(place)
        };

        const handleMarkerClick = (place) => {
          setViewport({ ...viewport, latitude: place.coordinate.latitude, longitude: place.coordinate.longitude });
          setShowPopup2(!showPopup2);
        };

  return (
    <Fragment>
      <Typography variant="h6" gutterBottom>
        Shipping details
      </Typography>
      <Grid container spacing={3}>
        <Grid item xs={12} sm = {6}>
          <TextField
            required
            id="title"
            label="Title"
            fullWidth
            autoComplete="given-name"
            onChange={handleTitleChange}
          />
        </Grid>
        <Grid item xs={12}>
          <TextField
            required
            id="description"
            label="Description"
            fullWidth
            autoComplete="family-name"
            onChange = {handleDescriptionChange}
          />
        </Grid>
        <Grid item xs={12}>
          <TextField
            id="landmark"
            label="LandMark"
            fullWidth
            autoComplete="shipping address-line2"
            onChange={handleLandMarkChange}
          />
        </Grid>
        <Grid item xs={12} sm={6}>
          {/* <TextField
            required
            id="city"
            name="city"
            label="Destination City"
            fullWidth
            autoComplete="shipping address-level2"
            onChange={handleCityChange}
          /> */}
          <div>
      <FormControl className={classes.formControl}>
        <InputLabel id="demo-simple-select-label">Select City</InputLabel>
        <Select
          labelId="demo-simple-select-label"
          id="demo-simple-select"
          // value={city}
          onChange={handleCityChange}
        >
          <MenuItem value='Mandi'>Mandi</MenuItem>
          <MenuItem value='Shimla'>Shimla</MenuItem>
          <MenuItem value='Delhi'>Delhi</MenuItem>
          <MenuItem value='Chandigarh'>Chandigarh</MenuItem>
        </Select>
      </FormControl>
      </div>
        </Grid>
        <Grid item xs={12} sm={6}>
          <TextField 
            id="state" 
            name="state" 
            label="State/Province/Region" 
            onChange={handleStateChange}
            fullWidth />
        </Grid>
        <Grid item xs={12} sm={6}>
          <TextField
            required
            id="zip"
            name="zip"
            label="Zip / Postal code"
            fullWidth
            autoComplete="shipping postal-code"
            onChange={handlePinCodeChange}
          />
        </Grid>
        <Grid item xs={12} sm={6}>
          <TextField
            required
            id="amount"
            name="amount"
            label="Amount"
            fullWidth
            autoComplete="shipping amount"
            onChange={handleAmountChange}
          />
        </Grid>
        <Grid item xs={12} sm={6}>
          <Typography>
          Country: India
          </Typography>
        </Grid>
        <Grid item xs={12}>
          <FormControlLabel
            control={<Checkbox color="secondary" name="saveAddress" value="yes" />}
            label="Use this address for payment details"
          />
        </Grid>
      </Grid>
      <div>
        <Typography>
          Set Location
        </Typography>
      <Box border={1} style={{ height: "70vh", width: "100%" }}>
                      
        <ReactMapGL
          {...viewport}
          mapboxApiAccessToken="pk.eyJ1IjoiYWJjaGFuZGFuMTEiLCJhIjoiY2tvdHU3ZWhjMDBmMDJ2bXZmdW54c3p5eiJ9.QNopaAC90pTKNW1bw-7bcg"
          width="100%"
          height="100%"
          transitionDuration="70"
          mapStyle="mapbox://styles/safak/cknndpyfq268f17p53nmpwira"
          onViewportChange={(viewport) => setViewport(viewport)}
          onDblClick={handleAddClick }
        >
          <Marker
                latitude={loc.latitude}
                longitude={loc.longitude}
                offsetLeft={-3.5 * viewport.zoom}
                offsetTop={-7 * viewport.zoom}
                
                
              >
                <Room
                  style={{
                    fontSize: 7 * viewport.zoom,
                    color:
                    "tomato",
                    cursor: "pointer",
                  }}
                  onClick={(e) => setShowPopup1(!showPopup1) }
                />
                
              </Marker>
                    {showPopup1 && 
                    (
                <Popup
                  longitude={loc.longitude}
                  latitude={loc.latitude}
                  closeButton={true}
                  closeOnClick={false}
                  offsetLeft={10}
                  offsetTop={-60}
                  onClose={() => setShowPopup1(false)}
                  anchor="bottom"
                  style={{borderColor: '#fff', borderRadius: 5}}
                >
                  <Typography>You are here</Typography>
                </Popup>
              )}
              {place && <Marker
                  latitude={place.coordinate.latitude}
                  longitude={place.coordinate.longitude}
                  offsetLeft={-3.5 * viewport.zoom}
                  offsetTop={-7 * viewport.zoom}
                >
                <Room
                  style={{
                    fontSize: 7 * viewport.zoom,
                    color:
                    "blue",
                    cursor: "pointer",
                  }}
                  onClick={() => handleMarkerClick( place)}
                />
                </Marker>
              }
              {place && showPopup2 && 
                  (<Popup
                    longitude={place.coordinate.longitude}
                    latitude={place.coordinate.latitude}
                    closeButton={true}
                    closeOnClick={false}
                    offsetLeft={10}
                    offsetTop={-60}
                    onClose={() => setShowPopup2(false)}
                    anchor="bottom"
                    style={{borderColor: '#fff', borderRadius: 5}}
                  >
                    <Typography>Destination coordinates:</Typography>
                    <Typography>Place: Shimla</Typography>
                    <Typography>Amount: 500</Typography>
                  </Popup>
                  )
              }
              
        </ReactMapGL>
      </Box> 
      </div>
     
    </Fragment>
  );
}

AddPostOptions.propTypes = {
  classes: PropTypes.object,
};

export default withStyles(styles, { withTheme: true })(AddPostOptions);
