import React, { Fragment, useState } from "react";
import PropTypes from "prop-types";
import { Button, Box } from "@material-ui/core";
import ActionPaper from "../../../shared/components/ActionPaper";
import ButtonCircularProgress from "../../../shared/components/ButtonCircularProgress";
import AddPostOptions from "./AddPostOptions";
import axios from 'axios';

function AddPost(props) {
  const {
    pushMessageToSnackbar,
    onClose,
    setPosts,
    posts
  } = props;

  const [loading, setLoading] = useState(false);

  const [title, setTitle] = useState('')
  const [description, setDescription] = useState('')
  const [landmark, setLandMark] = useState('')
  const [city, setCity] = useState('')
  const [pincode, setPinCode] = useState('')
  const [state, setState] = useState('')
  const [amount, setAmount] = useState('')
  const [destination, setDestination] = useState(null)

  const handleUpload = async () => {

    if(destination == null){
      alert('Select Destination coordinate Double click on the map to select a destination');
      return;
    }
    console.log(city)
    setLoading(true);
    const newPost = {
      title: title,
      description: description,
      landmark: landmark,
      coordinate: destination.coordinate,
      pincode: pincode,
      amount: amount,
      date: new Date()
    }

      const response = await axios.post('/api/markers', newPost);
      // return response.data
      setPosts([...posts, response.data])
      console.log(response.data)

    setTimeout(() => {
      pushMessageToSnackbar({
        text: "Your post has been uploaded",
      });
      onClose();
    }, 1500);
  };

  const handleTitleChange = (event) => {
    // console.log(event.target.value)
    setTitle(event.target.value)
  }

  const handleDescriptionChange = (event) => {
    // console.log(event.target.value)
    setDescription(event.target.value)
  }

  const handleLandMarkChange = (event) => {
    // console.log(event.target.value)
    setLandMark(event.target.value)
  }

  const handleCityChange = (event) => {
    // console.log(event.target.value)
    setCity(event.target.value)
  }

  const handlePinCodeChange = (event) => {
    // console.log(event.target.value)
    setPinCode(event.target.value)
  }

  const handleStateChange = (event) => {
    // console.log(event.target.value)
    setState(event.target.value)
  }

  const handleAmountChange = (event) => {
    // console.log(event.target.value)
    setAmount(event.target.value)
  }

  const handleDestinationChange = (event) => {
    // console.log(event.target.value)
    console.log(event.lngLat)
    const [longitude, latitude] = event.lngLat;
          setDestination({
            coordinate: {
              latitude: latitude,
              longitude: longitude,
            }
          });
  }


  return (
    <Fragment>
      <ActionPaper
        helpPadding
        maxWidth="md"
        content={
          <AddPostOptions  
          handleTitleChange={handleTitleChange}
          handleDescriptionChange={handleDescriptionChange}
          handleCityChange={handleCityChange}
          handleAmountChange={handleAmountChange}
          handleLandMarkChange={handleLandMarkChange}
          handleStateChange={handleStateChange}
          handlePinCodeChange={handlePinCodeChange}
          handleDestinationChange={handleDestinationChange}
          />
        }
        actions={
          <Fragment>
            <Box mr={1}>
              <Button onClick={onClose} disabled={loading}>
                Back
              </Button>
            </Box>
            <Button
              onClick={handleUpload}
              variant="contained"
              color="secondary"
            >
              Upload {loading && <ButtonCircularProgress />}
            </Button>
          </Fragment>
        }
      />
    </Fragment>
  );
}

AddPost.propTypes = {
  pushMessageToSnackbar: PropTypes.func,
  onClose: PropTypes.func,
};

export default AddPost;
